import { useCallback, useEffect, useMemo, useRef, useState } from "react";

import { Box, ButtonGroup, FormControl, Grid, IconButton, InputLabel, MenuItem, Select, Stack, Typography } from "@mui/material";
import { serializeAnimation, deserializeAnimation } from '../../midi/animations/util';
import { IMidiAnimation, IMidiAnimationJSON, ViewMode } from '../../midi/types';
import { useWavejs } from "../../midi/useWavejs";
import { createDemoAnimation } from "../../midi/demo";
import { Track } from "./types";
import TrackList from "./TrackList";
import Timeline from "./Timeline";
import { BaseAnimationOptions, OptionsPrototype } from "../../midi/animations/types";
import { MidiAnimationRegistry } from "../../midi/registryWithRef";
import "../../sass/index.scss";
import { groupAndCombineTracks } from "../../lib";
import EditDialog from "./EditDialog";
import { NamedUrl } from "../../midi/load";
import hacks from "../../lib/hacks";
import DownloadIcon from '@mui/icons-material/Download';
import SaveIcon from '@mui/icons-material/Save';
import prefs from "../../midi/prefs";

interface EditorProps {
  midiUrl?: NamedUrl;
  audioUrl?: NamedUrl;
  audioName?: string;
  tracks: Track[];
}

interface AnimationAndOptions {
  animation: IMidiAnimationJSON;
  optionsPrototype: OptionsPrototype;
}

const Editor = (props: EditorProps) => {
  const { midiUrl, audioUrl, tracks } = props;

  const { wave, audioElm, canvasElm } = useWavejs();

  const [selectedTrackIndex, setSelectedTrackIndex] = useState<number | undefined>(0);
  const [registry, setRegistry] = useState<null | MidiAnimationRegistry>(null);
  const [animationToEdit, setAnimationToEdit] = useState<AnimationAndOptions | null>(null);
  const [animationIndexToEdit, setAnimationIndexToEdit] = useState<number | undefined>(0);
  const [viewMode, setViewMode] = useState<ViewMode>("widescreen");

  const groupedAndCombinedTracks = useMemo(() => groupAndCombineTracks(tracks), [tracks]);

  const selectedTrack = useMemo(() => {
    return selectedTrackIndex === undefined ? undefined : groupedAndCombinedTracks[selectedTrackIndex];
  }, [groupedAndCombinedTracks, selectedTrackIndex]);

  const maxTrackDuration = useMemo(() => {
    return tracks.reduce((acc, track) => {
      return Math.max(acc, track.maxTimeSecs);
    }, 0);
  }, [tracks]);

  const [animations, setAnimations] = useState<IMidiAnimation[] | undefined>(undefined);
  const animationsRef = useRef<IMidiAnimation[] | undefined>(undefined);

  useEffect(() => {
    if (!animations) return;
    // XXX
    animationsRef.current = animations;
  }, [animations]);

  // TODO: Use state instead
  const animationsAndOptions = useMemo(() => (animations || []).map(anim => {
    const animation = serializeAnimation(anim);
    const optionsPrototype = anim.optionsPrototype;
    return {
      animation,
      optionsPrototype
    };
  }), [animations]) as AnimationAndOptions[];

  useEffect(() => {
    if (!groupedAndCombinedTracks) return;
    const animations = groupedAndCombinedTracks.map((track, i) => {
      const origin = { x: 110, y: 110 };
      const anim = createDemoAnimation(i, track.name, origin);
      return anim;
    });
    setAnimations(animations);
  }, [groupedAndCombinedTracks]);

  // TODO: Put in a hook
  useEffect(() => {
    const viewMode = prefs.get<ViewMode>("viewMode");
    setViewMode(viewMode);
  }, [setViewMode]);

  useEffect(() => {
    prefs.put("viewMode", viewMode);
  }, [viewMode]);

  useEffect(() => {
    console.log("midiUrl", midiUrl.name, "animations", animationsAndOptions);
    if (!midiUrl) return;
    if (!animationsRef.current) return;

    const registry = new MidiAnimationRegistry(animationsRef, { verbose: true });
    setRegistry(registry);
  }, [animationsRef.current]);

  useEffect(() => {
    console.log("wave", wave, "registry", registry);
    if (!wave) return;
    if (!registry) return;

    const init = async () => {
      await wave.addPlugin(registry, { eagerInit: false });
      await registry.initFromMidi(midiUrl.url);
    }
    init();
  }, [registry, wave]);

  const handleEditDialogOpenClose = useCallback(() => {
    setAnimationToEdit(null);
    setAnimationIndexToEdit(undefined);
  }, [setAnimationToEdit, setAnimationIndexToEdit]);

  const isEditDialogOpen = useMemo(() => !!animationToEdit, [animationToEdit]);
  const handleEditDialogOpenSave = useCallback((newOptions: BaseAnimationOptions) => {
    if (!newOptions) return;
    if (animationIndexToEdit === undefined) return;

    console.log("newOptions", newOptions);
    setAnimations(prev => {
      if (!prev) return prev;
      const anim = prev[animationIndexToEdit];
      anim.options = newOptions;
      return [...prev];
    });
    handleEditDialogOpenClose();
  }, [registry, animations, handleEditDialogOpenClose, animationIndexToEdit]);

  const playTrack = (track: number) => {
    console.log("TODO: playTrack", track);
  }
  const openTrackToEdit = useCallback((animation: number) => {
    setAnimationIndexToEdit(animation);
  }, [registry, animationsAndOptions, setAnimationToEdit]);

  useEffect(() => {
    if (animationIndexToEdit === undefined) return;
    const anim = animationsAndOptions[animationIndexToEdit];
    setAnimationToEdit(anim);
  }, [animationIndexToEdit]);

  const saveAnimation = useCallback((animationIndex: number) => {
    console.log("TODO: saveTrack", animationIndex);
  }, [registry, animationsAndOptions]);

  const cancelAnimationEdit = useCallback((animationIndex: number) => {
    console.log("TODO: cancelAnimationEdit", animationIndex);
  }, [registry, animationsAndOptions]);

  const muteAnimation = useCallback((animationIndex: number, newVal: boolean) => {
    setAnimations(prev => {
      if (!prev) return prev;
      const anim = prev[animationIndex];
      anim.mute = newVal;
      return [...prev];
    });
  }, [registry, animations]);

  const soloAnimation = useCallback((animationIndex: number, newVal: boolean) => {
    setAnimations(prev => {
      if (!prev) return prev;
      const anim = prev[animationIndex];
      anim.solo = newVal;
      return [...prev];
    });
  }, [registry, animations]);

  const updateAnimation = useCallback((animationIndex: number, newVal: IMidiAnimationJSON) => {
    setAnimations(prev => {
      if (!prev) return prev;
      try {
        const anim = deserializeAnimation(newVal);
        prev[animationIndex] = anim;
        return [...prev];
      } catch (e) {
        console.error("Error updating animation", e);
      }
      return prev;
    });
  }, [registry, animations]);

  console.log("animationsAndOptions", animationsAndOptions);
  console.log("groupedAndCombinedTracks", groupedAndCombinedTracks);

  const Tracks = () => {
    return (<>
      {!animationsAndOptions.length || !groupedAndCombinedTracks.length || animationsAndOptions.length !== groupedAndCombinedTracks.length
        ? undefined
        : <TrackList
          tracks={groupedAndCombinedTracks}
          animations={animationsAndOptions}
          selectedTrack={selectedTrackIndex}
          setSelectedTrack={setSelectedTrackIndex}
          play={playTrack}
          edit={openTrackToEdit}
          save={saveAnimation}
          cancel={cancelAnimationEdit}
          mute={muteAnimation}
          solo={soloAnimation}
          viewMode={viewMode}
        />
      }
    </>);
  }

  const saveAll = useCallback(() => {
    console.log("saveAll");

  }, []);

  const download = useCallback(() => {
    console.log("download");
  }, []);

  const saveAllEnabled = true;
  const downloadEnabled = true;

  const UpperLeft = () => {
    return (
      <Stack spacing={1} sx={{ mr: 1, mb: 1 }}>
        <Typography variant="subtitle1">
          {midiUrl.name ?? "none"}
        </Typography>

        <Box sx={{ display: "flex" }}>
          <Box sx={{ flexGrow: 1, justifySelf: "flex-start" }}>
            <ButtonGroup>
              <IconButton disabled={!saveAllEnabled} onClick={saveAll}>
                <SaveIcon />
              </IconButton>
              <IconButton disabled={!downloadEnabled} onClick={download}>
                <DownloadIcon />
              </IconButton>
            </ButtonGroup>
          </Box>
          <Box sx={{ justifySelf: "flex-end", width: 150, textAlign: "right" }}>
            <FormControl size="small" sx={{ width: 140 }}>
              <InputLabel id="view-mode-select-label">View mode</InputLabel>
              <Select
                labelId="view-mode-select-label"
                id="view-mode-select"
                value={viewMode}
                label="View mode"
                onChange={(e) => setViewMode(e.target.value as ViewMode)}
                sx={{ textAlign: "center" }}
              >
                <MenuItem value={"laptop"}>Laptop</MenuItem>
                <MenuItem value={"widescreen"}>Widescreen</MenuItem>
              </Select>
            </FormControl>
          </Box>
        </Box>
      </Stack>
    );
  }

  return (
    <div style={{ maxWidth: "100vw", width: "100vw", height: "100vh", maxHeight: "100vh", overflow: "hidden", margin: 10, }}>
      {
        animationToEdit ? <EditDialog
          isOpen={isEditDialogOpen}
          onSave={handleEditDialogOpenSave}
          onClose={handleEditDialogOpenClose}
          animation={animationToEdit}
        /> : undefined
      }
      <Grid container spacing={1}>
        <Grid item xs={3}>
          <UpperLeft />
        </Grid>
        <Grid item xs={9}>
          {!audioUrl
            ? undefined
            : <audio controls
              src={audioUrl.url}
              ref={audioElm}
              style={{
                width: '98%',
                margin: "auto",
              }}>
            </audio>}
        </Grid>
        <Grid item xs={3}>
          {groupedAndCombinedTracks.length > 0 ? <Tracks /> : undefined}
        </Grid>
        <Grid item xs={9} sx={{ p: 0, m: 0 }}>
          <Stack spacing={0}
            sx={{
              overflow: "hidden",
            }}
          >
            <Box
              sx={{
                height: hacks[viewMode].TIMELINE_AND_CANVAS_HEIGHT,
                p: 0,
                m: 0,
              }}>
              <Timeline
                duration={maxTrackDuration}
                selectedTrack={selectedTrack} />
              <Box sx={{
                // TODO: This is sloppy
                height: 1200,
                // minWidth: 1200,
                width: "100%",
                overflow: "always",
                p: 0,
                m: 0,
              }}>
                <main style={{ margin: 0 }} className={"p-home"}>
                  <section style={{ margin: 0 }} className={"__display"}>
                    <div style={{ borderRadius: 0 }} className={"__canvas"}>
                      <canvas ref={canvasElm} style={{ overflow: "auto" }} />
                    </div>
                  </section>
                </main>
              </Box>
            </Box>
          </Stack>
        </Grid>
      </Grid >
    </div >
  );
}

export default Editor;