import { Key } from "@mui/icons-material";
import { Prefs, Pref, Key as BaseKey } from "../lib/prefs";

type Key = BaseKey & ("viewMode");

const defaults = [
  {
    key: "viewMode",
    defaultValue: "widescreen",
  },
] as Pref<Key>[];

const prefs = new Prefs<Key>(defaults);

export default prefs;