import { v4 as uuidv4 } from "uuid";
import { NoteFilterCriteria } from "../types";
import { BaseAnimationOptions, OptionsPrototype } from "./types";

export interface Ctrl {
  mute: boolean;
  solo: boolean;
  disabled: boolean;
}

export class BaseMidiAnimation<O extends BaseAnimationOptions> {
  public readonly criteria: NoteFilterCriteria;
  public readonly options: O;
  public readonly id: string;
  public readonly name: string;
  public readonly optionsPrototype: OptionsPrototype;
  public readonly mute: boolean = false;
  public readonly solo: boolean = false;
  public readonly disabled: boolean = false;

  constructor(criteria: NoteFilterCriteria, options: O, name: string, optionsPrototype: OptionsPrototype, ctrl?: Ctrl, id?: string) {
    const { mute = false, solo = false, disabled = false } = ctrl || {};
    this.criteria = criteria;
    this.options = options;
    this.name = name;
    this.id = id || uuidv4();
    this.mute = mute;
    this.solo = solo;
    this.disabled = disabled;
    this.optionsPrototype = {
      origin: "point",
      fillColor: "fill",
      glow: "glow",
      lineColor: "fill",
      lineWidth: "number",
      ...optionsPrototype,
    };
  }
}
