import React, { useCallback } from "react";

import { IconButton, List, ListItem as MuiListItem, ListItemProps, ListItemSecondaryAction, Checkbox } from "@mui/material";
import { GroupedTrack } from "./types";
import TrackPanel, { AnimationAndOptions } from "./TrackPanel";
import EditIcon from "@mui/icons-material/Edit";
import ExpandLessIcon from "@mui/icons-material/ExpandLess";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import { styled } from "@mui/material/styles";
import hacks from "../../lib/hacks";
import MicIcon from '@mui/icons-material/Mic';
import MicOffIcon from '@mui/icons-material/MicOff';
import MicOffOutlinedIcon from '@mui/icons-material/MicOffOutlined';
import MicOutlinedIcon from '@mui/icons-material/MicOutlined';
import { IMidiAnimationJSON, ViewMode } from "../../midi/types";


interface TrackListProps {
  tracks: GroupedTrack[],
  animations?: AnimationAndOptions[],
  selectedTrack: number,
  setSelectedTrack: (track: number) => void,
  play: (index: number) => void,
  edit: (index: number) => void,
  save: (index: number) => void,
  cancel: (index: number) => void,
  mute: (index: number, muted: boolean) => void,
  solo: (index: number, soloed: boolean) => void,
  viewMode: ViewMode,
}

// Make the list item a little more like an accordion
// https://mui.com/material-ui/react-accordion/#customization
const ListItem = styled((props: ListItemProps) => (
  <MuiListItem  {...props} />
))(({ theme }) => ({
  backgroundColor:
    theme.palette.mode === "dark"
      ? "rgba(255, 255, 255, .05)"
      : "rgba(0, 0, 0, .03)",
  border: `1px solid ${theme.palette.divider}`,
  "&:not(:last-child)": {
    borderBottom: 0,
  },
  "&::before": {
    display: "none",
  },
}));

const TrackList = (props: TrackListProps) => {
  const {
    tracks, animations, setSelectedTrack, selectedTrack,
    play, edit, save, cancel, mute, solo,
    viewMode,
  } = props;


  const handleSingleSelection = useCallback((value: number) => {
    setSelectedTrack(value);
  }, [setSelectedTrack]);

  const [expanded, setExpanded] = React.useState<number | false>(false);

  const handleChange = (panel: number) => (event: React.SyntheticEvent, newExpanded: boolean) => {
    // setExpanded(newExpanded ? panel : false);
  };

  const handleExpandClick = (panel: number) => {
    setExpanded(prev => prev === panel ? false : panel);
  };

  return (<>
    <List
      sx={{
        overflow: "auto",
        height: hacks[viewMode].TRACKLIST_HEIGHT,
        p: 0,
        m: 0,
      }}
    >
      {tracks.map((track, i) => {
        const selected = selectedTrack === i;
        // TODO: Vary this a little
        // TODO: These are coming from the demo register

        const isExpanded = expanded === i;
        const animation = (animations ?? [])[i] || null;

        return <ListItem
          key={`track-${i}`}
          onClick={isExpanded ? () => handleExpandClick(i) : selected ? undefined : () => handleSingleSelection(i)}
          sx={{
            cursor: selected ? "initial" : "pointer",
            backgroundColor: selected ? "rgba(0, 0, 0, .1)" : undefined,
          }}
          dense
        >
          <TrackPanel
            i={i}
            track={track}
            expanded={isExpanded}
            handleChange={handleChange}
            playTrack={() => play(i)}
            save={() => save(i)}
            cancel={() => cancel(i)}
            animation={animation}
            selected={selected}
          />
          {/* When we expand the accordian, remove the open button */}
          <ListItemSecondaryAction>
            <IconButton
              onClick={(e) => { e.preventDefault(); handleExpandClick(i) }}
            >
              {expanded === false ? <ExpandMoreIcon /> : <ExpandLessIcon />}
            </IconButton>
            {!isExpanded &&
              <IconButton
                onClick={(e) => { e.preventDefault(); edit(i) }}
              >
                <EditIcon />
              </IconButton>
            }
            {!isExpanded &&
              <Checkbox
                inputProps={{ "aria-label": "solo" }}
                icon={<MicIcon />}
                checkedIcon={<MicOutlinedIcon />}
                onChange={(event) => mute(i, event.target.checked)}
                checked={animation.animation.mute}
              />
            }
            {!isExpanded &&
              <Checkbox
                inputProps={{ "aria-label": "mute" }}
                icon={<MicOffOutlinedIcon />}
                checkedIcon={<MicOffIcon />}
                onChange={(event) => solo(i, event.target.checked)}
                checked={animation.animation.solo}
              />
            }
          </ListItemSecondaryAction>
        </ListItem>
      })}
    </List >
  </>);
};

export default TrackList;