import { IMidiAnimation } from "../types";
import { Circles } from "./Circles";
import { Cubes } from "./Cubes";
import { OptionsPrototype } from "./types";
import { IMidiAnimationJSON } from '../types';

export const serializeAnimation = (midiAnimation: IMidiAnimation): IMidiAnimationJSON => {
  const { id, name, criteria, mute, solo, disabled, options } = midiAnimation;
  return {
    id,
    name,
    criteria,
    options,
    mute,
    solo,
    disabled,
  };
}

export const deserializeAnimation = (json: IMidiAnimationJSON): IMidiAnimation => {
  const { id, name, criteria, mute, solo, disabled, options } = json;
  const crtl = { mute, solo, disabled };
  switch (name) {
    case "circles":
      return new Circles(criteria, options, crtl, id);
    case "cubes":
      return new Cubes(criteria, options, crtl, id);
    default:
      throw new Error(`Unknown animation type: ${name}`);
  }
}

const optionsPrototypeDefaults = {
  "string": "",
  "color": "",
  "number": 0,
  "boolean": false,
  "point": { x: 0, y: 0 },
  "glow": { color: "", strength: 0 },
  "fill": { gradient: [] as string[], rotate: 0 }, // TODO: handle { image: string }
}

export const toOptionsPrototypeDefaults = (o: OptionsPrototype) => {
  return Object.fromEntries(Object.entries(o).map(([key, type]) => {
    const val = optionsPrototypeDefaults[type];
    return [key, val];
  }));
}