
import { Box, Button, Typography, ButtonGroup, Grid, Stepper, Step, StepContent, StepLabel } from "@mui/material";
import { useState, useMemo, useCallback, useEffect } from "react";
import { NamedUrl } from "../midi/load";
import { useIndexedDB } from "react-indexed-db-hook";
import dayjs from "dayjs";
import { Setter } from "../lib/types";
import { v4 as uuidv4 } from 'uuid';

interface FileUploaderProps {
  midiUrl?: NamedUrl;
  audioUrl?: NamedUrl;
  workspaceKey?: string;
  setMidiUrl: Setter<NamedUrl>;
  setAudioUrl: Setter<NamedUrl>;
  setShouldLoadFiles: Setter<boolean>;
}

const FileUploader: React.FC<FileUploaderProps> = ({
  midiUrl: pageMidiUrl,
  audioUrl: pageAudioUrl,
  workspaceKey: pageWorkspaceKey,
  setMidiUrl: pageSetMidiUrl,
  setAudioUrl: pageSetAudioUrl,
  setShouldLoadFiles,
}) => {
  const files = useIndexedDB("file");
  const ws = useIndexedDB("workspace");

  const [audioUrl, setAudioUrl] = useState<NamedUrl | undefined>(pageAudioUrl);
  const [midiUrl, setMidiUrl] = useState<NamedUrl | undefined>(pageMidiUrl);
  const [workspaceKey, setWorkspaceKey] = useState<string>(pageWorkspaceKey || uuidv4());

  const handleAudioFileChange = async (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.files && event.target.files.length > 0) {
      const f = event.target.files[0];
      setAudioUrl({ url: "", name: f.name });
      const url = await upload(f);
      setAudioUrl({ url, name: f.name });
    }
  };

  const handleMidiFileChange = async (event: React.ChangeEvent<HTMLInputElement>) => {
    console.log("handleMidiFileChange", event.target.files);
    if (event.target.files && event.target.files.length > 0) {
      const f = event.target.files[0];
      setMidiUrl({ url: "", name: f.name });
      const url = await upload(f);
      setMidiUrl({ url, name: f.name });
    }
  };

  const upload = async (file: File): Promise<string> => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onload = () => {
        const blobUrl = URL.createObjectURL(file);
        resolve(blobUrl);
      };
      reader.onerror = () => {
        reject(new Error("Failed to read file"));
      };
      reader.readAsArrayBuffer(file);
    });
  };

  const saveToDB = async (workspaceKey: string, audioUrl: NamedUrl, midiUrl: NamedUrl) => {
    // TODO: How to index?
    // Find or create workspace with key == workspaceKey
    const existingWorkspace = (await ws.getAll()).filter(w => w.key === workspaceKey)[0];
    let workspaceId: number;
    if (!existingWorkspace) {
      const workspace = await ws.add({
        key: workspaceKey,
        date: dayjs().toISOString(),
      });
      console.log("created workspace", workspace);
      workspaceId = workspace;
    } else {
      console.log("using existing workspace", existingWorkspace);
      workspaceId = existingWorkspace.id;
    }

    const fileExists = async (props: { checksum: string, name: string, type: string }) => {
      const { checksum, name, type } = props;

      // TODO: How to index?
      // const existing = await files.getByIndex("checksum", checksum);
      const existing = (await files.getAll()).filter(f => f.checksum === checksum)[0];
      if (!existing) {
        return false;
      }
      if (existing.name !== name) {
        console.warn("checksum sum but names differ", { existing, name });
        return false;
      }
      if (existing.type !== type) {
        console.warn("checksum sum but types differ", { existing, type });
        return false;
      }
      const updated = dayjs().toISOString();
      const update = await files.update({
        ...existing,
        updated,
      });
      console.log("update for existing", name, " = ", update);
      return true;
    }

    const md5Checksum = async (blob: Blob) => {
      const md5 = await crypto.subtle.digest("SHA-256", await blob.arrayBuffer());
      const checksum = new TextDecoder("utf-8").decode(md5);
      return checksum;
    }

    const createOrUpdateFile = async (props: { input: NamedUrl, type: string }) => {
      const { input: { url, name }, type } = props;
      const blob = await (await fetch(url)).blob();
      const checksum = await md5Checksum(blob);
      const exists = fileExists({ checksum, name, type });
      if (exists) {
        return;
      }
      const updated = dayjs().toISOString();
      const created = dayjs().toISOString();
      const added = await files.add({
        workspace_id: workspaceId,
        name,
        type,
        blob,
        checksum,
        created,
        updated,
      });
      return added;
    }

    const createFile = async (props: { input: Blob, name: string, type: string }) => {
      const { input: blob, name, type } = props;
      const checksum = await md5Checksum(blob);
      const updated = dayjs().toISOString();
      const created = dayjs().toISOString();
      const added = await files.add({
        workspace_id: workspaceId,
        name,
        type,
        blob,
        checksum,
        created,
        updated,
      });
      return added;
    }

    const [audioFile, midiFile, mdzFile] = await Promise.all([
      async function () {
        return await createOrUpdateFile({ input: audioUrl, type: "audio" });
      }(),
      async function () {
        return await createOrUpdateFile({ input: midiUrl, type: "midi" });
      }(),
      async function () {
        const midizBlob = new Blob([""], { type: "application/json" });
        const name = midiUrl.name.replace(/\.mid$/, ".mdz");
        return await createFile({ input: midizBlob, name, type: "midiz" });
      }(),
    ]);
    const update = await ws.update({
      id: workspaceId,
      audio_id: audioFile,
      midi_id: midiFile,
      midiz_id: mdzFile,
    });
    console.log("update", update);
  }

  const handleEditClick = useCallback(async () => {
    if (!audioUrl || !audioUrl.url || !audioUrl.name) return;
    if (!midiUrl || !midiUrl.url || !midiUrl.name) return;
    if (!workspaceKey) return;

    await saveToDB(workspaceKey, audioUrl, midiUrl);

    pageSetAudioUrl(audioUrl);
    pageSetMidiUrl(midiUrl);
    setShouldLoadFiles(true);
  }, [
    audioUrl,
    midiUrl,
    pageSetAudioUrl,
    pageSetMidiUrl,
    setShouldLoadFiles,
  ]);

  const handleClearClick = useCallback(() => {
    setAudioUrl(undefined);
    setMidiUrl(undefined);
    pageSetAudioUrl(undefined);
    pageSetMidiUrl(undefined);
  }, []);

  const handleClearAudioClick = useCallback(() => {
    setAudioUrl(undefined);
    pageSetAudioUrl(undefined);
  }, []);

  const handleClearMidiClick = useCallback(() => {
    setMidiUrl(undefined);
    pageSetMidiUrl(undefined);
  }, []);

  const haveEither = useMemo(() => {
    return !!audioUrl || !!midiUrl;
  }, [audioUrl, midiUrl]);

  const haveAudio = useMemo(() => {
    return !!audioUrl;
  }, [audioUrl]);

  const haveMidi = useMemo(() => {
    return !!midiUrl;
  }, [midiUrl]);

  const initialStep = useMemo(() => {
    if (haveAudio && haveMidi) {
      return 2;
    }
    if (haveAudio) {
      return 1;
    }
    return 0;
  }, [haveAudio, haveMidi]);

  const [activeStep, setActiveStep] = useState(initialStep);

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  return (<>
    <Box>
      <Stepper activeStep={activeStep} orientation="vertical">
        <Step>
          <StepLabel>
            Audio
            {audioUrl && (
              <Typography variant="subtitle1">{audioUrl.name} ({audioUrl.url ? "uploaded" : "uploading..."})</Typography>
            )}
          </StepLabel>
          <StepContent>
            <Typography>Upload an audio file (e.g. mp3 or wav)</Typography>
            <Box sx={{ mb: 2 }}>
              <div>
                {audioUrl ?
                  <Button
                    onClick={handleNext}
                  >
                    Next
                  </Button>
                  : <>
                    <input
                      type="file"
                      accept="audio/*"
                      onChange={(e) => { handleAudioFileChange(e); handleNext(); }}
                      style={{ display: "none" }}
                      id="audio-file-input"
                    />
                    <label htmlFor="audio-file-input">
                      <Button component="span">
                        Upload
                      </Button>
                    </label>
                  </>}
                <Button
                  disabled={false}
                  onClick={() => { handleClearAudioClick() }}
                >
                  Clear
                </Button>
              </div>
            </Box>
          </StepContent>
        </Step>
        <Step>
          <StepLabel>
            MIDI
            {midiUrl && (
              <Typography variant="subtitle1">{midiUrl.name} ({midiUrl.url ? "uploaded" : "uploading..."})</Typography>
            )}
          </StepLabel>
          <StepContent>
            <Typography>Upload a MIDI file. Why? Your browser never has the instruments in your MIDI file, so playing the MIDI file doesn't sound good.</Typography>
            <Box sx={{ mb: 2 }}>
              <div>
                {midiUrl ?
                  <Button
                    onClick={handleNext}
                  >
                    Next
                  </Button>
                  : <>
                    <input
                      type="file"
                      accept=".mid"
                      onChange={(e) => { handleMidiFileChange(e); handleNext(); }}
                      style={{ display: "none" }}
                      id="midi-file-input"
                    />
                    <label htmlFor="midi-file-input">
                      <Button component="span">
                        Upload
                      </Button>
                    </label>
                  </>}
                {midiUrl && <Button
                  disabled={false}
                  onClick={handleClearMidiClick}
                >
                  Clear
                </Button>
                }
                <Button
                  disabled={false}
                  onClick={handleBack}
                >
                  Back
                </Button>
              </div>
            </Box>
          </StepContent>
        </Step>
        <Step>
          <StepLabel>
            Edit
          </StepLabel>
          <StepContent>
            <Typography>Use these files to edit animations</Typography>
            <Box sx={{ mb: 2 }}>
              <div>
                {midiUrl && audioUrl ?
                  <>
                    <Button
                      onClick={handleEditClick}
                      disabled={!haveEither}
                    >
                      Upload
                    </Button>
                    <Button
                      onClick={() => { handleClearClick(); handleReset() }}
                      disabled={!haveEither}
                    >
                      Clear
                    </Button>
                  </>

                  : <>

                  </>}
                <Button
                  disabled={false}
                  onClick={handleBack}
                >
                  Back
                </Button>
              </div>
            </Box>
          </StepContent>
        </Step>
      </Stepper>
    </Box>
  </>);
}

export default FileUploader;