export type Key = string;

type ValidType = string | number | boolean;

export type Pref<K extends Key> = {
  key: K;
  defaultValue: ValidType;
}

export class Prefs<K extends Key> {

  private readonly defaults: Map<Key, Pref<Key>> = new Map();

  public constructor(defaults: Pref<Key>[]) {
    defaults.forEach(p => this.defaults.set(p.key, p));
  }

  public get<T>(key: K): T | undefined {
    const s = localStorage.getItem(key);
    if (!s) {
      const val = this.defaults.get(key).defaultValue;
      if (val === undefined) {
        throw new Error(`get: No default value for key: ${key}`);
      }
      return val as T;
    }
    return JSON.parse(s) as T;
  }

  public put<T>(key: K, value: T) {
    if (!this.defaults.has(key)) {
      throw new Error(`put: No such key: ${key}`);
    }
    const val = JSON.stringify(value);
    localStorage.setItem(key, val);
  }
}